
.navbar{
  background: #cae437;
  padding: 8px 16px;
}
ul{
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}
li{
  display: inline-block;
  padding: 5px;
}
li a{
    text-decoration: none;
    color: #9c1b4b;
    font-size: 17px;
    font-weight: 600;
    margin: 10px 5px;
}
.active{
  color:blue;
}
li a:hover,li a:active{
  color: white;
}